.item {
    min-width: 370px;
    padding: 15px;
    display: flex;
    align-items: center;
    gap: 10px;

    border: 1px solid rgb(231, 222, 222);
    
}

.item:hover{
    box-shadow: 4px 3px 4px 2px rgb(112, 221, 144);
}

.status {
    width: 15px;
    height: 15px;

    border-radius: 50%;
    background-color: black;
}

.online {
    width: 16px;
        height: 16px;
    
        border-radius: 50%;
        background-color: green;
}